@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.card{color:var(--text);font-size:14px;font-family:"Nunito Sans", sans-serif;display:flex;flex-direction:column;background-color:var(--elements);width:264px;margin:0 auto;margin-bottom:50px;border-radius:0.5em;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15);transition:transform ease-out 200ms;cursor:pointer}.card__flag{height:160px;object-fit:cover;border-radius:0.5em 0.5em 0 0}.card__container{margin:1.8em 2em;margin-bottom:3em}.card__title{margin-bottom:1em}.card__list{list-style:none;line-height:1.75em}.card__list-item--bold{font-weight:600}.card:hover{transform:scale(1.05)}@media (min-width: 650px){.card{margin:0;margin-bottom:5.2em}}

.input{position:relative;width:100%;max-width:480px;margin:1.6rem 0}.input__icon{position:absolute;top:50%;margin-top:-7px;left:2.5em;margin-left:calc(-7px / 2);color:var(--Input)}.input__field{color:var(--text);display:flex;width:100%;background-color:var(--elements);border:none;border-radius:0.5em;padding:1.1rem 5em;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15)}.input__field::-webkit-input-placeholder{font-size:14px;font-family:"Nunito Sans", sans-serif;color:var(--Input)}.input__field:-ms-input-placeholder{font-size:14px;font-family:"Nunito Sans", sans-serif;color:var(--Input)}.input__field::-ms-input-placeholder{font-size:14px;font-family:"Nunito Sans", sans-serif;color:var(--Input)}.input__field::placeholder{font-size:14px;font-family:"Nunito Sans", sans-serif;color:var(--Input)}@media (min-width: 1000px){.input{margin:0}}

.select-box{position:relative;display:flex;flex-direction:column;justify-content:space-between;width:200px;font-family:"Nunito Sans", sans-serif;color:var(--text);background-color:var(--elements);border:none;border-radius:0.5em;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15)}.select-box__wrapper{position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:200px}.select-box__trigger{position:relative;display:flex;align-items:center;justify-content:space-between;padding:1em 2em;cursor:pointer}.select-box__options{color:var(--text);border-radius:0.5em;position:absolute;display:block;top:110%;left:0;right:0;border-top:0;background:var(--elements);transition:all 0.5s;z-index:2;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15);padding:1em 0;opacity:0;visibility:hidden;pointer-events:none}.select-box__option{position:relative;display:block;padding:0.3em 2em;cursor:pointer;transition:all 0.5s}.select-box__options--open{opacity:1;visibility:visible;pointer-events:all}.select-box__option:hover{transform:translate(1em)}

.list__container--flex{display:flex;flex-direction:column;width:90%;max-width:1280px;margin:0 auto}.list__container--wrap{justify-content:space-between;max-width:500px;margin-top:4rem;flex-direction:row;flex-wrap:wrap}@media (min-width: 650px){.list__container--wrap{max-width:600px}}@media (min-width: 1000px){.list__container--flex{margin-top:3rem;flex-direction:row;align-items:center;justify-content:space-between}.list__container--wrap{max-width:900px}}@media (min-width: 1300px){.list__container--wrap{max-width:1280px}}

.header{font-family:"Nunito Sans", sans-serif;background-color:var(--elements);color:var(--text);padding:30px 0;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15)}.header__container{display:flex;justify-content:space-between;align-items:center;width:90%;max-width:1280px;margin:0 auto}.header__title{font-size:1rem;cursor:pointer}.header__toggle-container{display:flex}.header__dark-mode{margin-left:0.666em}.checkbox{height:0;width:0;visibility:hidden}.switch{cursor:pointer;display:inline-block;position:relative}@media (min-width: 600px){.header__title{font-size:1.5rem;letter-spacing:1px;line-height:0}.header__dark-mode{font-weight:600}}

.details{color:var(--text);font-family:"Nunito Sans", sans-serif;display:flex;flex-direction:column;width:85%;margin:0 auto;margin-top:2.8em}.details__flag{height:230px;width:100%;max-width:560px;object-fit:cover;margin-bottom:3.5em}.details__main-container{margin-top:4.5em}.details__title{font-size:1.4em;margin-bottom:1em}.details__list{list-style:none;line-height:2.25em;margin-bottom:2em}.details__list--horizontal{display:flex;flex-wrap:wrap}.details__list-item--bold{font-weight:600}.details__list-item--bigger{font-weight:600;font-size:1em;flex-basis:100%}.details__btn{color:var(--text);font-size:13px;line-height:2em;font-family:"Nunito Sans", sans-serif;text-align:center;border:none;background-color:var(--elements);padding:0 2em;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.15);border-radius:0.25em;margin-right:0.8em;margin-top:1em;cursor:pointer;transition:transform ease-out 200ms}.details__btn--back{padding:0.5em 1em;margin:0;width:30%;max-width:135px;box-shadow:0px 2px 16px -5px rgba(0,0,0,0.25)}.details__btn:last-child{margin-right:0}.details__btn-arrow{color:var(--text);margin-right:0.5em}.details__btn:hover{transform:scale(1.05)}@media (min-width: 600px){.details{font-size:16px}.details__flag{height:400px}}@media (min-width: 600px){.details__flag{height:400px}}@media (min-width: 700px){.details__main-container{display:flex;flex-direction:column;align-items:center}.details__text-container{margin-bottom:3em;max-width:560px}.details__list-container{display:flex;justify-content:space-between;width:560px}}@media (min-width: 1400px){.details{margin-top:5rem;width:1280px}.details__flag{margin:0}.details__list--horizontal{margin-bottom:0;align-items:center}.details__title{font-size:2em}.details__list-item--bigger{line-height:0;margin-top:0.5em;margin-right:1em;flex-basis:auto}.details__main-container{margin-top:5em;display:flex;align-items:center;flex-direction:row}.details__text-container{margin-left:140px}}

.App{background-color:var(--background);min-height:100vh;height:100%;padding-bottom:0.1px}

html{--elements: hsl(209, 23%, 22%);--background: hsl(207, 26%, 17%);--text: hsl(0, 0%, 100%);--Input: hsl(0, 0%, 100%);font-size:14px}html[data-theme="light"]{--elements: hsl(0, 0%, 100%);--background: hsl(0, 0%, 98%);--text: hsl(200, 15%, 8%);--Input: hsl(0, 0%, 52%)}*{margin:0;padding:0;box-sizing:border-box}html.transition,html.transition *,html.transition *:before,html.transition *:after{transition:background-color 750ms !important;transition-delay:0 !important}@media (min-width: 1000px){html{font-size:16px}}

